@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,400;0,700;0,800;0,900;1,400;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,400;0,700;0,800;0,900;1,400;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url(/Findair/static/media/background.4ed6da55.png);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Mulish', sans-serif;
}

#content > h1 {
  color: white;
  font-size: 64px;
  font-weight: 900;
  padding-top: 5%;
}

.App {
  padding-left: 4%;
  padding-right: 4%;
  padding-top: 2%;
}

span {
  color: #FCE205;
}

#content {
  padding-left: 3%;
  padding-right: 3%;
}
.visuallyHidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.placeSelect {
    width: 25vw;
}

form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    background-color: white;
    padding: 1vw;
    border-radius: 6px;
    margin-bottom: 2vh;
}

#searchOptions {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

#currencySelect {
    width: 6vw;
}

#roundtrip {
    margin-right: 1vw;
}

#rightOptions {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

#sortSelect {
    width: 13vw;
    margin-right: 1vw;
}

.searchInput {
    margin-right: 1vw;
}

button {
    font-family: 'Mulish', sans-serif;
    padding: 8px 12px;
    border-color: transparent;
    border-radius: 6px;
    background-color: transparent;
    color: white;
    font-weight: 700;
    font-size: 16px;
    cursor: pointer;
    transition-duration: 0.4s;
}

button:hover {
    border: 1pt solid #b3b3b3;
}

#search {
    background-color: #FCE205;
    font-weight: 800;
    color: black;
    border: none;
}

#search:hover {
    background-color: black;
    color: #FCE205;
}
.flights {
    color: black;
    margin-top: 2vh;
}

.flight {
    background-color: white;
    height: 100px;
    border-radius: 10px;
    padding: 10px 30px;
    margin-bottom: 2vh;
}
.header {
    min-height: 10vh;
    display: -webkit-flex;
    display: flex; 
    -webkit-flex-direction: row; 
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

#title {
    color: white;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-size: 32px;
}

a {
    text-decoration: none;
}

#logo {
    padding-right: 16px;
}
